<template>
    <el-dialog title="设置临时验证码" :visible.sync="dialogUpdate" class="updatePwd">
        <el-form :model="updatePwd">
            <el-form-item label="手机号码:" :label-width="formLabelWidth">
                <el-input v-model="updatePwd.telephone" auto-complete="off" disabled></el-input>
            </el-form-item>
            <el-form-item label="验证码:" :label-width="formLabelWidth">
                <el-input v-model="updatePwd.captcha" type = 'num' auto-complete="off" maxlength="4"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogUpdate = false">取 消</el-button>
            <el-button type="primary" @click="submitPwd()">提交</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../../core/QuRequest'

    const quRequest = new QuRequest()

    export default {
        data() {
            return {
                dialogUpdate: false,
                formLabelWidth: '120px',
                updatePwd:{
                    telephone: '',
                    captcha:''
                }
            }
        },
        methods: {
            showDialog(telephone) {
                if(telephone){
                    this.updatePwd.telephone = telephone;
                } else {
                    this.updatePwd.telephone = '';
                }
                this.dialogUpdate = true;
            },
            submitPwd(){
                quRequest.send(quRequest.Apis.qutuiba.member.setSmsCaptcha,this.updatePwd)
                    .then(res => {
                        //this.updatePwd.password = '';
                        this.dialogUpdate = false;
                        this.$message.success('设置临时验证码成功！');
                    });
            }
        }
    }
</script>


